import React, { useState } from "react"
import { Dialog, Transition } from '@headlessui/react'

interface Dialog {
    title: string,
    description?: string,
    bodyCopy?: string,
    imageUrl?: string,
    roleAlt?: string,
    isOpen: boolean,
    setIsOpen: any,
    email?: string
}

const MyDialog = (props: Dialog) => {
    return (
        <Transition appear show={props.isOpen} as={React.Fragment}>
            <Dialog
                as="div" className="fixed z-[1000] w-full height-full bg-[rgba(0,0,0,.75)] backdrop-blur-sm top-0 left-0"
                onClose={() => props.setIsOpen(false)}>

                <div className="min-h-screen px-4 text-center">
                    <Transition.Child
                        as={React.Fragment}
                        enter="ease-out duration-300"
                        enterFrom="opacity-0"
                        enterTo="opacity-100"
                        leave="ease-in duration-200"
                        leaveFrom="opacity-100"
                        leaveTo="opacity-0"
                    >
                        <Dialog.Overlay className="fixed inset-0" />
                    </Transition.Child>

                    {/* This element is to trick the browser into centering the modal contents. */}
                    <span
                        className="inline-block h-screen align-middle"
                        aria-hidden="true"
                    >
                        &#8203;
                    </span>
                    <Transition.Child
                        as={React.Fragment}
                        enter="ease-out duration-300"
                        enterFrom="opacity-0 scale-95"
                        enterTo="opacity-100 scale-100"
                        leave="ease-in duration-200"
                        leaveFrom="opacity-100 scale-100"
                        leaveTo="opacity-0 scale-95"
                    >
                        <div className="inline-block w-full max-w-6xl max-h-[60vh] overflow-hidden text-left align-middle transition-all transform bg-white text-black shadow-xl rounded-2xl">
                            <div className="grid md:grid-cols-2">
                                <div className="grid place-content-center mb-6 md:mb-0 max-h-[60vh]">
                                    <figure className="flex place-content-center rounded-full overflow-hidden md:rounded-none max-h-[60vh] h-[60vh]">
                                        <img className="object-cover max-w-none h-full" src={props.imageUrl} />
                                    </figure>
                                </div>

                                <div className="p-8 max-h-[50vh] grid grid-row-[auto_1fr]">
                                    <div>
                                        <Dialog.Title className="font-bold">{props.title}</Dialog.Title>
                                        <p className="text-xs mb-2">{props.roleAlt}</p>
                                        <p className="text-xs font-semibold">{props.email}</p>
                                    </div>

                                    <div className="overflow-scroll">

                                        <div className="mt-6" >
                                            <p className="text-sm text-gray-500 border-t pt-4" dangerouslySetInnerHTML={{ __html: props.description || "" }} />
                                        </div>

                                        <div className="mt-4">
                                            <button
                                                type="button"
                                                className="inline-flex justify-center px-4 py-2 text-sm text-yellow bg-black border border-transparent rounded-md hover:bg-red-200 duration-300"
                                                onClick={() => props.setIsOpen(false)}
                                            >
                                                Close
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Transition.Child>
                </div>
            </Dialog>
        </Transition>
    )
}

export default MyDialog