import * as React from "react"
import { useQuery } from "react-query";
import { getWPGraphQLData } from "../api/my-api";
import meetTheTeamHeaderBg from "../images/meet-the-team-header-bg.webp"
import MyDialog from "../components/dialog";

interface teamMember {
    title: string,
    content: string
    teamMember_ACF: {
        profilePhoto: {
            mediaItemUrl: string
        },
        role: string,
        roleAlt: string,
        email: string,
        buttonLabel: string
    }
}

const MeetTheTeam = () => {
    const [isOpen, setIsOpen] = React.useState(false)
    const [title, setTitle] = React.useState('')
    const [roleAlt, setRoleAlt] = React.useState('')
    const [description, setDescription] = React.useState('')
    const [email, setEmail] = React.useState('')
    const [imageUrl, setImageUrl] = React.useState('')


    const WP_GQL_QUERY = `
        query {
            teamMembersCPT(first: 100) {
                nodes {
                  teamMember_ACF {
                    role
                    profilePhoto {
                      mediaItemUrl
                    }
                    roleAlt
                    email
                    buttonLabel
                  }
                  title
                  content
                }
              }
        }
    `;

    const { isLoading, isError, data, error } = useQuery('getHomeContent', () => getWPGraphQLData(WP_GQL_QUERY))

    function openModal() {
        setIsOpen(true)
    }

    return (
        <div className={`page-wrapper`}>
            <section
                className="sub-hero"
            // style={{ backgroundImage: `url(${meetTheTeamHeaderBg})` }}
            >
                <div className="image-wrap">
                    <img src={meetTheTeamHeaderBg} alt="Meet The Team Header BG Image" />
                </div>
                <article>
                    <h1>
                        Meet The Team
                    </h1>

                    <p>
                        We have a fun and friendly group of trainers and staff who take pride in our fitness community and welcome every new face who walks in our doors. Our expertise, encouragement, service, and quest to assure you good health are benefits we proudly acknowledge.
                    </p>
                </article>
            </section>

            <main className={`main`}>
                <section className="
                    grid grid-cols-1 
                    sm:grid-cols-2 
                    md:grid-cols-3 
                    lg:grid-cols-4 
                    xl:grid-cols-[repeat(5,_minmax(0,322px))] 
                    place-content-center 
                    place-items-center 
                    items-start 
                    gap-y-6 
                    gap-7
                ">
                    {
                        !isLoading && data && data.teamMembersCPT &&
                        data.teamMembersCPT.nodes.map((member: teamMember, key: number) => {
                            return (
                                <div
                                    key={key}
                                    className="
                                        grid 
                                        gap-3 
                                        bg-transparent
                                        text-black 
                                        rounded-xl 
                                        cursor-pointer 
                                        min-w-[250px] 
                                        sm:min-w-0 
                                        max-w-[300px]
                                    "
                                    onClick={() => {
                                        setTitle(member.title)
                                        setRoleAlt(member.teamMember_ACF.roleAlt)
                                        setDescription(member.content)
                                        setEmail(member.teamMember_ACF.email)
                                        setImageUrl(member.teamMember_ACF.profilePhoto.mediaItemUrl)
                                        openModal()
                                    }}
                                >
                                    <figure className="
                                        rounded-2xl 
                                        overflow-hidden 
                                        max-h-[400px] 
                                        h-[400px]
                                    ">
                                        <img className="h-full object-cover" src={member.teamMember_ACF.profilePhoto.mediaItemUrl} />
                                    </figure>

                                    <div className="
                                        grid 
                                        place-items-center 
                                        text-center
                                    ">
                                        <h2 className="font-bold text-xl">{member.title}</h2>
                                        <p className="text-xs uppercase">{member.teamMember_ACF.role}</p>
                                    </div>
                                </div>
                            )
                        })
                    }
                </section>
            </main>

            <MyDialog
                title={title}
                roleAlt={roleAlt}
                description={description}
                isOpen={isOpen}
                setIsOpen={setIsOpen}
                email={email}
                imageUrl={imageUrl}
            />
        </div>
    )
}

export default MeetTheTeam